"use client"
import React, { useEffect, useState } from "react"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { motion } from "framer-motion"
import { PieChart, Pie, Cell, ResponsiveContainer, Tooltip } from "recharts"
import { Search } from "lucide-react"

type Applicant = {
  id: number
  name: string
  gender: string
  program: string
  status?: string
  created_at: string
}

const Dashboard = () => {
  const [applicants, setApplicants] = useState<Applicant[]>([])
  const [searchTerm, setSearchTerm] = useState("")

  // Fetch applicants data
  useEffect(() => {
    fetch("/api/applicants") // replace with your actual API
      .then((res) => res.json())
      .then((data) => setApplicants(data))
      .catch((err) => console.error("Error fetching applicants:", err))
  }, [])

  const normalizeStatus = (status: string | undefined) =>
    status?.trim().toLowerCase() ?? ""

  // ✅ Simplified and corrected display mapping
  const mapStatusForDisplay = (status: string | undefined) => {
    const normalized = normalizeStatus(status)
    return normalized
  }

  // ✅ Calculate counts correctly
  const submittedCount = applicants.filter(
    (app) => normalizeStatus(app.status) === "submitted"
  ).length
  const pendingCount = applicants.filter(
    (app) => normalizeStatus(app.status) === "pending"
  ).length
  const approvedCount = applicants.filter(
    (app) => normalizeStatus(app.status) === "approved"
  ).length
  const rejectedCount = applicants.filter(
    (app) => normalizeStatus(app.status) === "rejected"
  ).length

  // ✅ Status chart data
  const data = [
    { name: "Submitted", value: submittedCount },
    { name: "Pending", value: pendingCount },
    { name: "Approved", value: approvedCount },
    { name: "Rejected", value: rejectedCount },
  ]

  const COLORS = ["#6b7280", "#facc15", "#10b981", "#ef4444"]

  // ✅ Filtered list for search
  const filteredApplicants = applicants.filter((app) =>
    app.name.toLowerCase().includes(searchTerm.toLowerCase())
  )

  return (
    <div className="p-6 space-y-6">
      <h1 className="text-3xl font-semibold mb-4">Applications Dashboard</h1>

      {/* Search bar */}
      <div className="relative w-full max-w-sm mb-6">
        <input
          type="text"
          placeholder="Search applicants..."
          className="w-full rounded-xl border px-10 py-2 focus:outline-none focus:ring focus:ring-blue-200"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
        <Search className="absolute left-3 top-2.5 text-gray-400" size={18} />
      </div>

      {/* Stats summary */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        <StatCard label="Submitted" value={submittedCount} />
        <StatCard label="Pending" value={pendingCount} />
        <StatCard label="Approved" value={approvedCount} />
        <StatCard label="Rejected" value={rejectedCount} />
      </div>

      {/* Chart */}
      <Card className="mt-6">
        <CardContent>
          <h2 className="text-xl font-semibold mb-4">Application Status Breakdown</h2>
          <div className="h-64">
            <ResponsiveContainer width="100%" height="100%">
              <PieChart>
                <Pie
                  data={data}
                  dataKey="value"
                  nameKey="name"
                  cx="50%"
                  cy="50%"
                  outerRadius={100}
                  label
                >
                  {data.map((entry, index) => (
                    <Cell key={index} fill={COLORS[index % COLORS.length]} />
                  ))}
                </Pie>
                <Tooltip />
              </PieChart>
            </ResponsiveContainer>
          </div>
        </CardContent>
      </Card>

      {/* Applicant List */}
      <Card className="mt-6">
        <CardContent>
          <h2 className="text-xl font-semibold mb-4">Applicant List</h2>
          <div className="overflow-x-auto">
            <table className="min-w-full border-collapse">
              <thead>
                <tr className="bg-gray-100">
                  <th className="text-left py-2 px-3">Name</th>
                  <th className="text-left py-2 px-3">Program</th>
                  <th className="text-left py-2 px-3">Status</th>
                  <th className="text-left py-2 px-3">Date</th>
                </tr>
              </thead>
              <tbody>
                {filteredApplicants.map((app) => (
                  <tr key={app.id} className="border-b hover:bg-gray-50">
                    <td className="py-2 px-3">{app.name}</td>
                    <td className="py-2 px-3">{app.program}</td>
                    <td className="py-2 px-3 capitalize">
                      {mapStatusForDisplay(app.status)}
                    </td>
                    <td className="py-2 px-3">
                      {new Date(app.created_at).toLocaleDateString()}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}

const StatCard = ({ label, value }: { label: string; value: number }) => (
  <motion.div
    whileHover={{ scale: 1.03 }}
    transition={{ type: "spring", stiffness: 200 }}
  >
    <Card className="shadow-md rounded-2xl">
      <CardContent className="p-4 text-center">
        <h3 className="text-lg font-medium text-gray-600">{label}</h3>
        <p className="text-3xl font-bold mt-2">{value}</p>
      </CardContent>
    </Card>
  </motion.div>
)

export default Dashboard
